body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Playfair Display", serif !important;
  background-image: url(/static/media/recipeLaptop.ae65b634.jpg);
  background-size: cover;
}

.search {
  color: white;
  font-weight: bold;
}

.text {
  font-family: "Oxygen", sans-serif;
  color: white;
}

label {
  color: white;
}

.card {
  font-family: "Oxygen", sans-serif;
}

.red {
  color: red;
}

.gold {
  color: gold;
}

.buttonDetails {
  background-color: #4ca64c;
  color: #fff;
  transition: .5s;
}

.buttonURL {
  background-color: red;
  color: #fff;
  transition: .5s;
}

.buttonGold {
  background-color: gold;
  color: #4ca64c;
  transition: .5s;
}

.buttonDetails:hover {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}

.buttonURL:hover {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  color: white;
}

buttonGold:hover {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  color: #4ca64c;
}

.secondaryColor {
  color: #4ca64c;
  font-weight: bold;
}

.btn-primary {
  transition: .5s;
}

.btn-primary:hover {
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}

.buttonSecondary {
  background-color: red;
  color: #fff;
  transition: .5s;
}

.buttonSecondary:hover {
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  color: #fff;
}

.card {
  border: 1px solid gold;
}

/*iPad Pro*/
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) 
  and (-webkit-min-device-pixel-ratio: 1.5) {
    body {
      background-image: url(/static/media/recipeIpadPro.7169c0fe.jpg);
    }



    .d-block {
      position: relative;
      top: 117px;
      height: 600px;
    }
}

@media screen and (max-width: 990px) {
  body {
    background-image: url(/static/media/recipe2.00b477cc.jpg);
    background-size: auto;
  }
}

@media screen and (max-width: 768px)  {
  body {
    background-image: url(/static/media/recipe3.02e813e6.jpg);
    background-size: auto;
  }

  .buttonDetails, .buttonURL {
    padding: 3px;
  }

  .d-block {
    position: relative;
    top: 135px;
    height: 400px;
  }
}

@media screen and (max-width: 425px)  {
  body {
    background-image: url(/static/media/recipe5.7f5e3928.jpg);
  }

  .d-block {
    position: static;
    height: 300px;
  }
}

@media screen and (max-width: 360px)  {
  .d-block {
    height: 200px;
  }

  .buttonDetails, .buttonURL {
    padding: 10px;
  }
}
